// eslint-disable-next-line
import { Box, Chip, Divider, IconButton, Link, Tooltip, Typography } from "@mui/material";
import React from "react";
import { useNavigate } from "react-router-dom";
import { ExampleImages } from "../../static/exampleImages";
import MemoryCard from "../UI/MemoryCard";


const Start = () => {

    // eslint-disable-next-line
    const history = useNavigate();

    return (
        <Box component="main" className="flex-column w100">
            <Box className="flex-column gap-2">
                {Object.keys(ExampleImages).map((el) => (
                    <MemoryCard 
                        el={el}
                        key={Math.random()}
                    />
                ))}
                <Box className="flex-column gap-0">
                    <Typography variant="body1" className="note">Вы долистали до конца</Typography>
                    <Box className="flex-row wrap">
                        <Typography variant="body2" className="note">
                            <Link underline="always" className="note">Добавьте</Link>
                        </Typography>
                        <Typography variant="body2" className="note">&nbsp;свою фотографию в историю</Typography>
                    </Box>
                </Box>
            </Box>
        </Box>
    )
}

export default Start;