import { IconButton, Link, Tooltip, Typography } from "@mui/material";
import { Box } from "@mui/system";
import React from "react";
import { useNavigate } from "react-router-dom";
import PersonIcon from '@mui/icons-material/Person';
import AddIcon from '@mui/icons-material/Add';
import BookmarkIcon from '@mui/icons-material/Bookmark';
import SearchPreHeader from "./Search/SearchPreHeader";
import { ABOUT_ROUTE, STARTPAGE_ROUTE } from "../../routes/consts";

const HeaderOnPage = () => {

    // eslint-disable-next-line
    const history = useNavigate();

    const preHeaderItems = [
        [
            'Категории',
            
        ],
        [
            'Фото на карте',
            
        ],
        [
            'О проекте',
            ABOUT_ROUTE
        ],
    ]

    

    return (
        <Box
            component="header"
            className="flex-column wrap"
        >
            <Box className="flex-row w100 preheader gap-1 wrap jcsb">
                <Box className="flex-row gap-1 wrap">
                    {Object.keys(preHeaderItems).map(el => (
                        <Link
                            underline="hover"
                            key={Math.random()}
                            onClick={() => history(preHeaderItems[el][1])}
                        >
                            <Typography variant="body1">{preHeaderItems[el][0]}</Typography>
                        </Link>
                    ))}
                </Box>
                <Box className="flex-row gap-0">
                    <SearchPreHeader />
                    <Box className="flex-row gap-0">
                        <Tooltip title="Закладки" arrow>
                            <IconButton>
                                <BookmarkIcon />
                            </IconButton>
                        </Tooltip>
                        <Tooltip title="Добавить фото" arrow>
                            <IconButton>
                                <AddIcon />
                            </IconButton>
                        </Tooltip>
                        <Tooltip title="Аккаунт" arrow>
                            <IconButton>
                                <PersonIcon />
                            </IconButton>
                        </Tooltip>
                    </Box>
                </Box>
            </Box>
            <Box className="flex-column wrap" sx={{ my: 3 }}>
                <Typography 
                    variant="h1" 
                    sx={{ fontWeight: 400, cursor: 'pointer' }}
                    onClick={() => history(STARTPAGE_ROUTE)}
                >
                    earth.archive
                </Typography>
                <Typography variant="body1" sx={{ fontWeight: 400 }} className="note2">вы в ленте последних фото</Typography>
            </Box>
        </Box>
    )
}

export default HeaderOnPage;