import { Box, Chip, Divider, IconButton, Tooltip, Typography } from "@mui/material";
import React from "react";
import { ExampleImages } from "../../static/exampleImages";
import BookmarkIcon from '@mui/icons-material/Bookmark';
import CameraAltIcon from '@mui/icons-material/CameraAlt';
import PersonIcon from '@mui/icons-material/Person';
import EventIcon from '@mui/icons-material/Event';
import PlaceIcon from '@mui/icons-material/Place';
import ArrowOutwardIcon from '@mui/icons-material/ArrowOutward';

const MemoryCard = ({el}) => {

    return (
        <Box
            className="flex-column gap-0 memory"
            key={ExampleImages[el][0]}
        >
            <Box className="flex-row jcsb w50">
                <Box className="flex-row jcfs">
                    <IconButton disabled>
                        <PersonIcon fontSize="small" />
                    </IconButton>
                    <Typography variant="body2" className="note">
                        {ExampleImages[el][3]}
                    </Typography>
                </Box>
                <Tooltip title={ExampleImages[el][6]} arrow>
                    <Box className="flex-row jcfe">
                        <IconButton disabled>
                            <PlaceIcon fontSize="small" />
                        </IconButton>
                        <Typography variant="body2" className="note">
                            {ExampleImages[el][1]}
                        </Typography>
                    </Box>
                </Tooltip>
            </Box>
            <img
                src={ExampleImages[el][0]}
                alt={"test"}
            />
            <Box className="flex-row jcsb w50">
                <Box className="flex-row">
                    <Box className="flex-row gap-0">
                        <Tooltip title="Добавить в избранное" arrow>
                            <IconButton>
                                <BookmarkIcon fontSize="small" />
                            </IconButton>
                        </Tooltip>
                        <Tooltip title="Открыть фото" arrow>
                            <IconButton>
                                <ArrowOutwardIcon fontSize="small" />
                            </IconButton>
                        </Tooltip>
                    </Box>
                    <Tooltip title={ExampleImages[el][7]} arrow>
                        <Box className="flex-row gap-0 jcfe">
                            <IconButton disabled>
                                <CameraAltIcon fontSize="small" />
                            </IconButton>
                            <Typography variant="body2" className="note">
                                {ExampleImages[el][2]}
                            </Typography>
                        </Box>
                    </Tooltip>
                </Box>
                <Box className="flex-row">
                    <IconButton disabled>
                        <EventIcon fontSize="small" />
                    </IconButton>
                    <Typography variant="body2" className="note">
                        {ExampleImages[el][4]}
                    </Typography>
                </Box>
            </Box>
            <Box className="flex-row gap-0 wrap note2 jcfs w50">
                <Chip 
                    label={`#${ExampleImages[el][5]}`} 
                    variant="outlined" 
                    className="chip"
                />
            </Box>
            <Divider variant="middle" className="w40" sx={{ mt: 3 }}></Divider>
        </Box>
    )
}

export default MemoryCard;